	module.exports = {"data":{"markdownRemark":{"html":"<p>So the blog was up and then I was having all sorts of errors. Note that the links are for your edification but ultimately did not help me.</p>\n<p>tl;dr\nYou need backticks - not single quotes - for your <code>gatsby-config.js</code> filenames.</p>\n<ol>\n<li>\"Cannot read property 'allMarkdownRemark' of undefined\" <a href=\"https://github.com/DSchau/gatsby-blog-starter-kit/issues/9\">more here</a></li>\n<li>\"TypeError: Cannot read property 'allMarkdownRemark' of undefined\"</li>\n<li>\"gatsby Invariant Violation: GraphQLParser: Unknown argument <code>formatString</code>. Source: document <code>IndexQuery</code> file: <code>GraphQL request</code>\"</li>\n</ol>\n<p>These error messages ultimately were not incredibly helpful to a Noob like myself, as the pointed to the <code>index.js</code> root file which meant they could be anything.</p>\n<p>Through sheer luck / force of will / desperation, I started looking through tutorials, my go-to once Stack Overflow and Github comment threads start looking like junk.   </p>\n<p>So my initial <code>gatbsy-config.js</code> looks like so:</p>\n<pre><code class=\"language-javascript\">module.exports = {\n  siteMetadata: {\n    title: `Russell Schmidt`,\n    author: `Russell Schmidt`,\n  },\n  plugins: [\n    `gatsby-plugin-react-helmet`,\n    `gatsby-plugin-catch-links`,\n    {\n      resolve: `gatsby-source-filesystem`,\n      options: {\n        path: `${__dirname}/src/pages`,\n        name: 'pages',\n      },\n    },\n    {\n      resolve: `gatsby-transformer-remark`,\n      options: {\n        plugins: [] // for future interop with gatsby-remark-prismjs, gatsby-remark-copy-linked-files, gatsby-remark-images\n      }\n    },\n  ],\n};\n</code></pre>\n<p><a href=\"https://medium.freecodecamp.org/how-to-build-a-react-and-gatsby-powered-blog-in-about-10-minutes-625c35c06481\">This is a great tutorial</a> that unlocked the secrets of the universe.</p>","frontmatter":{"date":"February 09, 2018","path":"/blog/backticks/","title":"Gatsby Backticks","type":"blog"}}},"pathContext":{}}

